.booking-container {
    max-width: 900px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .booking-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .booking-table th,
  .booking-table td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .booking-table th {
    background-color: #f4f4f4;
  }
  
  .member-info {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .member-image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  
  .status {
    font-weight: bold;
    text-transform: capitalize;
  }
  
  .status.confirmed {
    color: green;
  }
  
  .status.pending {
    color: orange;
  }
  
  .status.cancelled {
    color: red;
  }
  
  .edit-btn,
  .delete-btn {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .edit-btn {
    background-color: #007bff;
    color: white;
    margin-right: 5px;
  }
  
  .delete-btn {
    background-color: #dc3545;
    color: white;
  }
  
  .edit-btn:hover {
    background-color: #0056b3;
  }
  
  .delete-btn:hover {
    background-color: #b02a37;
  }
  
  /* Edit Form */
  .edit-form {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    width: 300px;
  }
  
  .edit-form h3 {
    text-align: center;
  }
  
  .edit-form label {
    display: block;
    margin-top: 10px;
  }
  
  .edit-form input,
  .edit-form select {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .edit-form button {
    width: 100%;
    padding: 8px;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .edit-form button[type="submit"] {
    background-color: #28a745;
    color: white;
  }
  
  .edit-form button[type="button"] {
    background-color: #6c757d;
    color: white;
  }
  