/* General Layout */
.rides-container {
    font-family: "Arial", sans-serif;
    background-color: #f8f8f8;
    padding: 20px;
  }
  

  
  /* Header */
  .rides-header {
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    padding: 20px;
    color: #333;
    background-color: #ffffff;
    border-bottom: 2px solid #ddd;
  }
  
  /* Classic Form */
  .classic-form {
    background: white;
    padding: 20px;
    max-width: 500px;
    margin: 20px auto;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .form-title {
    text-align: center;
    font-size: 22px;
    margin-bottom: 15px;
    color: #333;
    font-weight: bold;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-row {
    display: flex;
    gap: 10px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  /* Search Button */
  .search-button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #0056b3;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .search-button:hover {
    background-color: #003d80;
  }
  
  /* Classic Vehicle Selection */
  .classic-vehicle-section {
    margin-top: 20px;
    padding: 20px;
    background: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .vehicle-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
  }
  
  .classic-vehicle-card {
    border: 1px solid #ddd;
    padding: 15px;
    border-radius: 8px;
    text-align: center;
    background: #fff;
  }
  
  .vehicle-image {
    width: 100px;
    height: auto;
  }
  
  .vehicle-name {
    font-weight: bold;
    margin-top: 5px;
  }
  
  .vehicle-capacity {
    font-size: 14px;
    color: gray;
  }
  